type Factory = (params?: unknown) => unknown;
type Handler = (element: Element) => void;

export const _createTagByFactoryName = (factory: Factory) => {
  return factory.name
    .split(/(?=[A-Z])/)
    .join("-")
    .toLowerCase();
};

export const createElementByFactoryName =
  (componentFactory: Factory, handler: Handler) => () => {
    const tagName = _createTagByFactoryName(componentFactory);
    const element = document.createElement(tagName);
    handler(element);
  };
