import type { TemplateSchema } from "@/types";
import { createChain } from "@/factories";
import { isFunction, isString } from "@/validators";
import { createElementByFactoryName } from "./createElementByFactoryName";
import { createElementByTagName } from "./createElementByTagName";

type Factory = (params?: unknown) => unknown;

export const createElement =
  ({ type, props, children }: TemplateSchema) =>
    (): Element => {
      let element!: Element;
      const _chain = createChain();

      _chain.add({
        validator: isString(type),
        action: createElementByTagName(type as string, (htmlElement: Element) => {
          element = htmlElement;
        }),
      });

      _chain.add({
        validator: isFunction(type),
        action: createElementByFactoryName(
          type as Factory,
          (htmlElement: Element) => {
            element = htmlElement;
          },
        ),
      });

      _chain.execute();

      for (const key in props) {
        const property = key as string;
        const value = props[key] as string;
        element.setAttribute(property, value);
      }

      for (const child of children) {
        createElement(child);
      }

      return element;
    };
